body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: scroll; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p {
  margin: 0;
  padding: 0; }

ul {
  padding: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 1024px;
  margin: 0 auto; }

@media (max-width: 966px) {
  .container {
    width: 100%;
    margin: 0 auto; } }

.header {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #ececec;
  -webkit-box-shadow: 0 0 20px #ececec;
          box-shadow: 0 0 20px #ececec;
  margin-bottom: 20px; }
  .header img {
    margin-top: 13px; }

.news__list {
  width: 100%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }
  .news__list li {
    width: 500px;
    margin: 0 auto;
    border: 1px solid #ececec;
    border-radius: 10px;
    list-style: none;
    -webkit-box-shadow: 0px 0px 14px 3px rgba(34, 60, 80, 0.2);
            box-shadow: 0px 0px 14px 3px rgba(34, 60, 80, 0.2);
    margin-bottom: 20px; }
  .news__list li:last-child {
    margin: 0;
    margin-left: 6px;
    margin-bottom: 20px; }

.news__user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 15px; }
  .news__user img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: block;
    margin-right: 10px; }
  .news__user p {
    display: block; }

.news__img {
  width: 100%;
  height: 250px;
  margin-bottom: 20px; }
  .news__img img {
    width: 100%;
    height: 100%; }

.news__text {
  padding: 15px; }
  .news__text h3 {
    margin-bottom: 5px; }
  .news__text p {
    margin-bottom: 10px; }

.news__data {
  width: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 15px; }
  .news__data p {
    margin-right: 5px; }

.slider {
  width: 98%;
  margin: 0 auto;
  margin-bottom: 20px; }
